body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f8f8f8;
}

*{
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.container{
  max-width: 1120px;
  margin: 0 auto;
}
.left-sidebar {
  width: 300px;
}
.right-part-box {
  width: calc(100% - 300px);
  padding-left: 30px;
}
.white-box{
  background-color: #fff;
  padding: 12px;
  margin-bottom: 15px;
}
.white-box h6{
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 10px;
}
.header-reset{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header-reset h6{
  margin-bottom: 0;
}
.header-reset button{
  padding: 0;
  margin: 0;
  width: auto;
  color: red;
}
.white-box ul li{
  padding: 0;
  font-size: 14px;
}
.white-box ul li span{
  font-size: 14px;
}